.blockquote {
  position: relative;
}

.blockquote p {
  font-size: 50px;
  font-weight: 100px;
  text-align: center;
  color: #0edb3efa;
  font-family: 'Gill Sans', 'Gill Sans MT', Calibri, 'Trebuchet MS', sans-serif;
}

.otro-blockquote span {
  text-decoration: underline;
}

.contributor {
  text-align: center;
  font-weight: bold;
  color: #0edb3efa;
  font-family: 'Gill Sans', 'Gill Sans MT', Calibri, 'Trebuchet MS', sans-serif;
  font-size: 50px;
  margin-top: 100px;
  margin-bottom: -50px;
}

