.blockquote::after {
  content: "";
  top: 20px;
  left: 50%;
  margin-left: -190px;
  position: absolute;
  /* border-bottom: 3px solid #242021; */
  height: 3px;
  width: 400px;
}

@import url(https://fonts.googleapis.com/css?family=Open+Sans:400italic);

.otro-blockquote {
  font-size: 1.4em;
  width: clamp(300px, 90%, 800px);
  margin: 50px auto;
  font-family: Open Sans;
  font-style: italic;
  color: #555555;
  padding: 1.2em 30px 1.2em 75px;
  border-left: 8px solid #78C0A8;
  line-height: 1.6;
  position: relative;
  background: #EDEDED;
}

.otro-blockquote::before {
  font-family: Arial;
  content: "\201C";
  color: #78C0A8;
  font-size: 4em;
  position: absolute;
  left: 10px;
  top: -10px;
}

.otro-blockquote {
  margin-top: 0px;
}

.otro-blockquote::after {
  content: '';
}

.otro-blockquote span {
  display: block;
  color: #333333;
  font-style: normal;
  font-weight: bold;
  margin-top: 1em;
}






@import url(https://fonts.googleapis.com/css?family=Raleway:400,200,300,800);
@import url(https://code.ionicframework.com/ionicons/2.0.1/css/ionicons.min.css);

figure.snip0056 {
  font-family: 'Raleway', Arial, sans-serif;
  position: relative;
  overflow: hidden;
  margin: 10px;
  min-width: 380px;
  max-width: 480px;
  width: 100%;
  background: #ffffff;
  color: #000000;
  box-shadow: 0px 0px 25px black;
}

figure.snip0056 * {
  -webkit-box-sizing: border-box;
  box-sizing: border-box;
}

figure.snip0056>img {
  width: 50%;
  border-radius: 50%;
  /* border: 1px solid #1b1919; */
  -webkit-transition: all 0.35s ease-in-out;
  transition: all 0.35s ease-in-out;
  -webkit-transform: scale(1.6);
  transform: scale(1);
  /* position: relative; */
  float: right;
  right: -15%;
  z-index: 1;
  height: 250px;
}

figure.snip0056 figcaption {
  padding: 20px 30px 20px 20px;
  position: absolute;
  left: 0;
  width: 50%;
}

figure.snip0056 figcaption h2,
figure.snip0056 figcaption p {
  margin: 0;
  text-align: left;
  padding: 100px 0;
  width: 100%;
}

figure.snip0056 figcaption h2 {
  font-size: 1.3em;
  font-weight: 300;
  text-transform: uppercase;
  border-bottom: 1px solid rgba(0, 0, 0, 0.2);
}

figure.snip0056 figcaption h2 span {
  font-weight: 800;
}

figure.snip0056 figcaption p {
  font-size: 0.9em;
  opacity: 0.8;
}

figure.snip0056 figcaption .icons {
  width: 100%;
  text-align: left;
}

figure.snip0056 figcaption .icons i {
  font-size: 26px;
  padding: 5px;
  top: 50%;
  color: #000000;
}

figure.snip0056 figcaption a {
  opacity: 0.3;
  -webkit-transition: opacity 0.35s;
  transition: opacity 0.35s;
}

figure.snip0056 figcaption a:hover {
  opacity: 0.8;
}

figure.snip0056 .position {
  width: 100%;
  text-align: left;
  padding: 15px 30px;
  font-size: 0.9em;
  opacity: 1;
  font-style: italic;
  color: #ffffff;
  background: #000000;
  clear: both;
}

figure.snip0056.blue .position {
  background: #20638f;
}

figure.snip0056.red .position {
  background: #962d22;
}

figure.snip0056.yellow .position {
  background: #bf6516;
}

figure.snip0056:hover>img,
figure.snip0056.hover>img {
  right: -12%;
}


